			       //      uploadBtn.style.display="none"
			       //     }
			    }
			    // 判断上传文件的尺寸，超过300k需压缩，再放入文件数组中
			    if(filesize<1024*300){
			        arrFiles.push({"file":file,name:filename,img:e.target.result});
			        // var cur=arrFiles.length-1;
			        // that.$mount()
			        // that.$refs['img'+cur][0].src=e.target.result;
			    }
			    else{
			        var tempImg=new Image()
			        tempImg.onload=function() {
			            var originWidth=tempImg.width;  //获取原图的宽高
			            var targetWidth=tempImg.width;
			            var originHeight=tempImg.height;
			            var targetHeight=tempImg.height;
			            var maxWidth=1120,maxHeight=680;
			            var filetype=file.type;
			            if(originWidth>maxWidth||originHeight>maxHeight){
			                if(originWidth/maxWidth>originHeight/maxHeight){
			                    targetWidth=maxWidth;
			                    targetHeight=originHeight*targetWidth/originWidth;
			                }
			                else{
			                    targetHeight=maxHeight;
			                    targetWidth=originWidth*targetHeight/originHeight
			                }
			            }
			            canvas.width=targetWidth
			            canvas.height=targetHeight;
			            ctx.clearRect(0,0,targetWidth,targetHeight)
			            ctx.drawImage(tempImg,0,0,targetWidth,targetHeight);
			            // 不能直接用canvas.toBlob，大部分手机浏览器不支持，
			            // 先读取成base64,再读取字符转为blob对象, // new File()也是兼容性不好
			            var dataurl=canvas.toDataURL(filetype,0.8);
			            // 0.8的压缩比对于image/jpeg生效，image/png不会生效

			            var bin=atob(dataurl.split(",")[1])
			            var len=bin.length;

			            var buffer=new Uint8Array(len)
			            for(var i=0;i<len;i++){
			                buffer[i]=bin.charCodeAt(i)
			            }
			            var blob=new Blob([buffer.buffer],{type:filetype});

			           	arrFiles.push({"file":blob,name:filename,'img':dataurl});		
			            img.src=dataurl;
			        }
			        tempImg.src=e.target.result;
			    }
			} 
